}

.tabList {
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	display: flex;
}

.tab {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 10px;
	flex: 1 1 100px;
	text-align: center;
	background: #f1f1f1;
	cursor: pointer;
	border-bottom: 2px solid rgba(0, 0, 0, 0.5);
	position: relative;
	outline: none;
}

.tab:not(:last-child) {
	border-right: 2px solid rgba(0, 0, 0, 0.5);
}

.selectedTab {
	background: #fff;
	border-bottom: 2px solid transparent;
}

.tabPanel {
	max-height: 100%;
	min-height: 0px;
	height: 100%;
	overflow: auto hidden;
}

.content {
	background: #fff;
	width: 100%;
	height: 100%;
	display: none;
}

